.product-list {
    display: flex; 
    flex-wrap: wrap; 
    padding: 0; 
    margin: 0; 
}

h2{
    font-size: 50px;
    font-weight: bold; 
    color: rgb(172, 117, 143);
    text-shadow: 5px 5px 5px rgb(221, 208, 214);
}

.product-item {
    flex: 0 0 calc(30%); 
    margin: 10px; 
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1); 
    padding: 10px; 
    border: 3px solid #c2779b; 
    border-radius: 5px; 
    text-align: center; 
}

.product-item img {
    max-width: 100%; 
    height: auto; 
}

.category-filter button {
    background-color:  #c47ea8; 
    color: white; 
    border: none; 
    padding: 5px 10px; 
    font-size: 0.9em; 
    border-radius: 3px; 
    margin: 0 5px; 
}

.category-filter button:hover {
    background-color:  #986182; 
}

.pagination {
    display: flex;
    justify-content: center;
    margin-top: 20px;
}

.pagination button {
    margin: 0 10px;
    padding: 5px 10px;
    font-size: 0.9em; 
}

.pagination span {
    align-self: center;
}

input[type="text"] {
    width: 50%; 
    padding: 10px; 
    border: 2px solid #986182; 
    border-radius: 4px; 
    margin-bottom: 20px; 
    font-size: 16px; 
}