.slider-container {
    position: relative;
    width: 90%; 
    max-width: 100%; 
    margin: 0 auto; 
    overflow: hidden; 
    margin-top: 40px;
}

.slider {
    display: flex;
    align-items: center;
    width: 100%; 
}

.slide {
    position: relative;
    width: 100%; 
}

.slide img {
    width: 100%; 
    height: auto; 
    border-radius: 10px; 
}

.text-overlay {
    position: absolute;
    top: 10%; 
    left: 50%;
    transform: translateX(-50%); 
    color: white; 
    text-align: center;
    background-color: rgba(218, 159, 178, 0.7);
    padding: 30px; 
    border-radius: 10px; 
    width: 80%; 
    max-width: 600px;
    height: 400px;
}

.text-overlay h3 {
    font-size: 3em; 
    margin: 30px; 
}

.text-overlay p {
    font-size: 1.2em; 
    margin: 60px 0 0; 
}

button {
    padding: 30px;
    margin: 0 10px;
    border: none;
    border-radius: 5px;
    font-size: 2em;
    background-color: #c47ea8;
    color: white;
}

button:hover {
    background-color: #986182;
}
