.modal-overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(0, 0, 0, 0.5);
    display: flex;
    justify-content: center;
    align-items: center;
}

.modal-content {
    background: white;
    padding: 20px;
    border-radius: 5px;
    max-width: 500px;
    width: 90%; 
    text-align: center;
}

.modal-content button {
    background-color: #c47ea8; 
    color: white; 
    border: none; 
    padding: 5px 10px; 
    font-size: 0.9em; 
    border-radius: 3px; 
    margin: 0 5px; 
}

.modal-content button:hover {
    background-color: #986182; 
}
