.footer {
    background-color: pink; 
    color: white; 
    padding: 40px 20px; 
    text-align: center; 
    position: relative; 
    bottom: -20px; 
    z-index: -10;
}

.footer-container {
    display: flex; 
    flex-direction: column; 
    align-items: center; 
}

.footer-section {
    margin-bottom: 20px; 
}

.social-links {
    list-style: none; 
    padding: 0; 
    margin-left: 19px;
}

.social-links li {
    display: inline; 
    margin: 0 10px; 
}

.footer-bottom {
    margin-top: 20px; 
}

.social-links {
    list-style: none;
    padding: 0;
    display: flex;
    gap: 10px; 
}

.social-links a {
    color: #ffffff; 
    text-decoration: none; 
}

h4{
    font-weight: bold;
    font-size: 18px;
}